<script setup lang="ts">
import { RouterLink, useRouter } from 'vue-router';
import { Paragraph } from '@/shared/ui/text/paragraph';
import { Logo } from '@/shared/ui/logo';
import { ref } from 'vue';
import { useStore } from 'vuex';

const store = useStore();
const router = useRouter();
const error = ref('');

const logout = async () => {
    try {
        await store.dispatch('logout'); 
        router.push({ name: 'admin-login' });
    } catch (err: any) {
        console.error('Ошибка при выходе:', err);
        error.value = 'Ошибка при выходе: ' + (err.response?.data?.detail || 'неизвестная ошибка');
    }
};

</script>

<template>
    <aside>
        <Logo />
        <ul>
            <li>
                <RouterLink to="/admin/users">
                    <Paragraph tagName="span" size="xxl" color="white">пользователи</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/doctors">
                    <Paragraph tagName="span" size="xxl" color="white">врачи</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/prices">
                    <Paragraph tagName="span" size="xxl" color="white">цены</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/cases">
                    <Paragraph tagName="span" size="xxl" color="white">кейсы</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/questions">
                    <Paragraph tagName="span" size="xxl" color="white">вопросы</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/contacts">
                    <Paragraph tagName="span" size="xxl" color="white">контакты</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/results">
                    <Paragraph tagName="span" size="xxl" color="white">результаты до / после</Paragraph>
                </RouterLink>
            </li>
            <li>
                <RouterLink to="/admin/promotions">
                    <Paragraph tagName="span" size="xxl" color="white">акции</Paragraph>
                </RouterLink>
            </li>
            <button @click="logout">выйти</button>
            <p v-if="error">{{ error }}</p> 
        </ul>
    </aside>
</template>

<style lang="scss" scoped>
aside {
    background: #23262F;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 25px;
    ul {
        li {
            padding: 25px 0;
        }
    }
}
</style>
